import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Alert,
  AlertDescription,
  AlertIcon,
  AlertTitle,
  Badge,
  Box,
  Button,
  Card,
  CardBody,
  CardHeader,
  Checkbox,
  Container,
  Flex,
  FormControl,
  FormLabel,
  Grid,
  GridItem,
  HStack,
  Heading,
  IconButton,
  Input,
  InputGroup,
  InputLeftElement,
  Modal,
  ModalBody,
  ModalCloseButton,
  ModalContent,
  ModalFooter,
  ModalHeader,
  ModalOverlay,
  Select,
  SimpleGrid,
  Spacer,
  Stat,
  StatHelpText,
  StatLabel,
  StatNumber,
  Switch,
  Table,
  TableContainer,
  Tbody,
  Td,
  Text,
  Textarea,
  Th,
  Thead,
  Tooltip,
  Tr,
  useColorModeValue,
  useDisclosure,
  useToast,
  VStack,
} from '@chakra-ui/react';

// Mock data para mantenimientos
const mockMaintenanceRecords = [
  {
    id: 1,
    vehiclePlate: 'ABC-123',
    vehicleBrand: 'Volvo FH16',
    type: 'Cambio de aceite',
    status: 'completed', // completed, scheduled, overdue
    scheduledDate: '2024-01-15',
    completedDate: '2024-01-15',
    nextDue: '2024-04-15',
    mileage: 45000,
    cost: 150.00,
    description: 'Cambio de aceite motor y filtro',
    technician: 'Juan Pérez',
    workshop: 'Taller Central',
    priority: 'normal', // low, normal, high, urgent
    isFuture: false,
  },
  {
    id: 2,
    vehiclePlate: 'DEF-456',
    vehicleBrand: 'Mercedes Actros',
    type: 'Revisión general',
    status: 'scheduled',
    scheduledDate: '2024-02-10',
    completedDate: null,
    nextDue: null,
    mileage: 52000,
    cost: 300.00,
    description: 'Revisión completa del sistema de frenos y suspensión',
    technician: 'Carlos Mendoza',
    workshop: 'Taller Norte',
    priority: 'high',
    isFuture: true,
  },
  {
    id: 3,
    vehiclePlate: 'GHI-789',
    vehicleBrand: 'Ford Transit',
    type: 'Cambio de llantas',
    status: 'overdue',
    scheduledDate: '2024-01-20',
    completedDate: null,
    nextDue: null,
    mileage: 89000,
    cost: 400.00,
    description: 'Cambio de 4 llantas traseras',
    technician: 'Ana López',
    workshop: 'Taller Sur',
    priority: 'urgent',
    isFuture: false,
  },
  {
    id: 4,
    vehiclePlate: 'ABC-123',
    vehicleBrand: 'Volvo FH16',
    type: 'Mantenimiento preventivo',
    status: 'scheduled',
    scheduledDate: '2024-03-01',
    completedDate: null,
    nextDue: null,
    mileage: 47000,
    cost: 250.00,
    description: 'Mantenimiento preventivo programado - Revisión de motor y transmisión',
    technician: 'Pedro Ramírez',
    workshop: 'Taller Central',
    priority: 'normal',
    isFuture: true,
  },
];

const Maintenance: React.FC = () => {
  const navigate = useNavigate();
  const toast = useToast();
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [currentPage, setCurrentPage] = useState(1);
  const [searchTerm, setSearchTerm] = useState('');
  const [statusFilter, setStatusFilter] = useState('all');
  const [priorityFilter, setPriorityFilter] = useState('all');
  const [selectedRecord, setSelectedRecord] = useState<any>(null);
  const [isEditing, setIsEditing] = useState(false);
  const [isCurrentMaintenance, setIsCurrentMaintenance] = useState(false);
  const [showFutureOnly, setShowFutureOnly] = useState(false);
  const itemsPerPage = 10;

  // Color mode values
  const cardBg = useColorModeValue('white', 'gray.800');
  const borderColor = useColorModeValue('gray.200', 'gray.700');
  const textColor = useColorModeValue('gray.800', 'white');
  const mutedTextColor = useColorModeValue('gray.500', 'gray.400');
  const inputBg = useColorModeValue('white', 'gray.700');
  const tableHeaderBg = useColorModeValue('gray.50', 'gray.700');
  const tableRowHover = useColorModeValue('gray.50', 'gray.700');
  const tableRowBg = useColorModeValue('white', 'gray.800');
  const scrollbarThumb = useColorModeValue('gray.300', 'gray.600');

  // Filtrar registros
  const filteredRecords = mockMaintenanceRecords.filter(record => {
    const matchesSearch = record.vehiclePlate.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         record.type.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         record.vehicleBrand.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesStatus = statusFilter === 'all' || record.status === statusFilter;
    const matchesPriority = priorityFilter === 'all' || record.priority === priorityFilter;
    const matchesFuture = !showFutureOnly || record.isFuture;
    
    return matchesSearch && matchesStatus && matchesPriority && matchesFuture;
  });

  // Paginación
  const totalPages = Math.ceil(filteredRecords.length / itemsPerPage);
  const startIndex = (currentPage - 1) * itemsPerPage;
  const paginatedRecords = filteredRecords.slice(startIndex, startIndex + itemsPerPage);

  // Estadísticas
  const stats = {
    total: mockMaintenanceRecords.length,
    completed: mockMaintenanceRecords.filter(r => r.status === 'completed').length,
    scheduled: mockMaintenanceRecords.filter(r => r.status === 'scheduled').length,
    overdue: mockMaintenanceRecords.filter(r => r.status === 'overdue').length,
    future: mockMaintenanceRecords.filter(r => r.isFuture).length,
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'completed': return 'green';
      case 'scheduled': return 'blue';
      case 'overdue': return 'red';
      default: return 'gray';
    }
  };

  const getStatusText = (status: string) => {
    switch (status) {
      case 'completed': return 'Completado';
      case 'scheduled': return 'Programado';
      case 'overdue': return 'Vencido';
      default: return 'Desconocido';
    }
  };

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case 'urgent': return 'red';
      case 'high': return 'orange';
      case 'normal': return 'blue';
      case 'low': return 'gray';
      default: return 'gray';
    }
  };

  const getPriorityText = (priority: string) => {
    switch (priority) {
      case 'urgent': return 'Urgente';
      case 'high': return 'Alta';
      case 'normal': return 'Normal';
      case 'low': return 'Baja';
      default: return 'Normal';
    }
  };

  const handleEdit = (record: any) => {
    setSelectedRecord(record);
    setIsEditing(true);
    onOpen();
  };

  const handleAddFuture = () => {
    setSelectedRecord(null);
    setIsEditing(false);
    setIsCurrentMaintenance(false);
    onOpen();
  };

  const handleSave = () => {
    let title, description;
    
    if (isEditing) {
      title = 'Registro actualizado';
      description = 'El registro de mantenimiento ha sido actualizado exitosamente.';
    } else if (isCurrentMaintenance) {
      title = 'Mantenimiento registrado';
      description = 'El mantenimiento actual ha sido registrado exitosamente.';
    } else {
      title = 'Mantenimiento programado';
      description = 'El mantenimiento futuro ha sido programado exitosamente.';
    }
    
    toast({
      title,
      description,
      status: 'success',
      duration: 3000,
      isClosable: true,
    });
    onClose();
  };

  return (
    <Container maxW="full" p={{ base: 4, md: 6, lg: 8 }}>
      <VStack spacing={{ base: 4, md: 6 }} align="stretch">
        {/* Header */}
        <Box textAlign={{ base: "center", md: "left" }}>
          <Heading size={{ base: "md", md: "lg" }} color={textColor} mb={2}>
            🔧 Gestión de Mantenimiento
          </Heading>
          <Text color={mutedTextColor} fontSize={{ base: "sm", md: "md" }}>
            Administra y programa el mantenimiento de toda tu flota vehicular
          </Text>
        </Box>

        {/* Estadísticas */}
        <Box 
          overflowX="auto" 
          pb={2}
          css={{
            '&::-webkit-scrollbar': {
              height: '6px',
            },
            '&::-webkit-scrollbar-track': {
              background: 'transparent',
            },
            '&::-webkit-scrollbar-thumb': {
              background: useColorModeValue('gray.300', 'gray.600'),
              borderRadius: '3px',
            },
          }}
        >
          <SimpleGrid 
            columns={{ base: 2, sm: 3, md: 5 }}
            spacing={{ base: 3, md: 4 }}
            mb={{ base: 4, md: 6 }}
          >
            <Card bg={cardBg} border="1px solid" borderColor={borderColor}>
              <CardBody p={{ base: 3, md: 4 }}>
                <Stat>
                  <StatLabel color={mutedTextColor} fontSize={{ base: 'xs', sm: 'sm' }}>Total</StatLabel>
                  <StatNumber color="blue.500" fontSize={{ base: 'lg', sm: 'xl', md: '2xl' }}>{stats.total}</StatNumber>
                  <StatHelpText fontSize={{ base: 'xs', sm: 'sm' }}>Registros</StatHelpText>
                </Stat>
              </CardBody>
            </Card>

            <Card bg={cardBg} border="1px solid" borderColor={borderColor}>
              <CardBody p={{ base: 3, md: 4 }}>
                <Stat>
                  <StatLabel color={mutedTextColor} fontSize={{ base: 'xs', sm: 'sm' }}>Completados</StatLabel>
                  <StatNumber color="green.500" fontSize={{ base: 'lg', sm: 'xl', md: '2xl' }}>{stats.completed}</StatNumber>
                  <StatHelpText fontSize={{ base: 'xs', sm: 'sm' }}>{Math.round((stats.completed / stats.total) * 100) || 0}%</StatHelpText>
                </Stat>
              </CardBody>
            </Card>

            <Card bg={cardBg} border="1px solid" borderColor={borderColor}>
              <CardBody p={{ base: 3, md: 4 }}>
                <Stat>
                  <StatLabel color={mutedTextColor} fontSize={{ base: 'xs', sm: 'sm' }}>Programados</StatLabel>
                  <StatNumber color="blue.500" fontSize={{ base: 'lg', sm: 'xl', md: '2xl' }}>{stats.scheduled}</StatNumber>
                  <StatHelpText fontSize={{ base: 'xs', sm: 'sm' }}>Activos</StatHelpText>
                </Stat>
              </CardBody>
            </Card>

            <Card bg={cardBg} border="1px solid" borderColor={borderColor}>
              <CardBody p={{ base: 3, md: 4 }}>
                <Stat>
                  <StatLabel color={mutedTextColor} fontSize={{ base: 'xs', sm: 'sm' }}>Vencidos</StatLabel>
                  <StatNumber color="red.500" fontSize={{ base: 'lg', sm: 'xl', md: '2xl' }}>{stats.overdue}</StatNumber>
                  <StatHelpText fontSize={{ base: 'xs', sm: 'sm' }}>Por atender</StatHelpText>
                </Stat>
              </CardBody>
            </Card>

            <Card bg={cardBg} border="1px solid" borderColor={borderColor}>
              <CardBody p={{ base: 3, md: 4 }}>
                <Stat>
                  <StatLabel color={mutedTextColor} fontSize={{ base: 'xs', sm: 'sm' }}>Futuros</StatLabel>
                  <StatNumber color="purple.500" fontSize={{ base: 'lg', sm: 'xl', md: '2xl' }}>{stats.future}</StatNumber>
                  <StatHelpText fontSize={{ base: 'xs', sm: 'sm' }}>Programados</StatHelpText>
                </Stat>
              </CardBody>
            </Card>
          </SimpleGrid>
        </Box>

        {/* Controles */}
        <Card bg={cardBg} border="1px solid" borderColor={borderColor} mb={{ base: 4, md: 6 }}>
          <CardBody p={{ base: 3, md: 4 }}>
            <VStack spacing={4} align="stretch">
              {/* Filtros - Todos los tamaños */}
              <Box>
                <Text fontSize="sm" fontWeight="medium" mb={2} color={mutedTextColor}>
                  Buscar y filtrar
                </Text>
                <SimpleGrid columns={{ base: 1, sm: 2, lg: 4 }} spacing={{ base: 2, md: 3 }}>
                  <InputGroup size="sm">
                    <InputLeftElement pointerEvents="none">
                      <Text color={mutedTextColor}>🔍</Text>
                    </InputLeftElement>
                    <Input
                      placeholder="Placa, tipo, marca..."
                      value={searchTerm}
                      onChange={(e) => setSearchTerm(e.target.value)}
                      fontSize={{ base: 'sm', md: 'md' }}
                    />
                  </InputGroup>

                  <Select 
                    value={statusFilter} 
                    onChange={(e) => setStatusFilter(e.target.value)}
                    size="sm"
                    fontSize={{ base: 'sm', md: 'md' }}
                  >
                    <option value="all">Todos los estados</option>
                    <option value="completed">Completados</option>
                    <option value="scheduled">Programados</option>
                    <option value="overdue">Vencidos</option>
                  </Select>

                  <Select 
                    value={priorityFilter} 
                    onChange={(e) => setPriorityFilter(e.target.value)}
                    size="sm"
                    fontSize={{ base: 'sm', md: 'md' }}
                  >
                    <option value="all">Todas las prioridades</option>
                    <option value="urgent">Urgente</option>
                    <option value="high">Alta</option>
                    <option value="normal">Normal</option>
                    <option value="low">Baja</option>
                  </Select>

                  <HStack spacing={2} align="center" justify={{ base: 'space-between', sm: 'flex-start' }}>
                    <Text fontSize={{ base: 'sm', md: 'md' }} color={mutedTextColor} whiteSpace="nowrap">
                      Solo futuros:
                    </Text>
                    <Switch
                      isChecked={showFutureOnly}
                      onChange={(e) => setShowFutureOnly(e.target.checked)}
                      colorScheme="purple"
                      size="md"
                    />
                  </HStack>
                </SimpleGrid>
              </Box>

              {/* Botones de acción */}
              <Box>
                <Text fontSize="sm" fontWeight="medium" mb={2} color={mutedTextColor}>
                  Acciones
                </Text>
                <VStack spacing={{ base: 2, sm: 3 }} align="stretch" display={{ base: 'flex', sm: 'none' }}>
                  <Button 
                    colorScheme="green" 
                    leftIcon={<Text>🔧</Text>} 
                    onClick={() => {
                      setSelectedRecord(null);
                      setIsEditing(false);
                      setIsCurrentMaintenance(true);
                      onOpen();
                    }}
                    size="sm"
                    fontSize="sm"
                    whiteSpace="normal"
                    height="auto"
                    py={3}
                    px={4}
                    textAlign="center"
                  >
                    Registrar Mantenimiento Actual
                  </Button>
                  <Button 
                    colorScheme="blue" 
                    leftIcon={<Text>📅</Text>} 
                    onClick={handleAddFuture}
                    size="sm"
                    fontSize="sm"
                    whiteSpace="normal"
                    height="auto"
                    py={3}
                    px={4}
                    textAlign="center"
                  >
                    Programar Mantenimiento Futuro
                  </Button>
                </VStack>
                
                <SimpleGrid columns={2} spacing={3} display={{ base: 'none', sm: 'grid' }}>
                  <Button 
                    colorScheme="green" 
                    leftIcon={<Text>🔧</Text>} 
                    onClick={() => {
                      setSelectedRecord(null);
                      setIsEditing(false);
                      setIsCurrentMaintenance(true);
                      onOpen();
                    }}
                    size="md"
                    fontSize="md"
                  >
                    Registrar Actual
                  </Button>
                  <Button 
                    colorScheme="blue" 
                    leftIcon={<Text>📅</Text>} 
                    onClick={handleAddFuture}
                    size="md"
                    fontSize="md"
                  >
                    Programar Futuro
                  </Button>
                </SimpleGrid>
              </Box>
            </VStack>
          </CardBody>
        </Card>

        {/* Alerta para mantenimientos futuros */}
        {showFutureOnly && (
          <Alert status="info" borderRadius="lg" mb={{ base: 4, md: 6 }}>
            <AlertIcon />
            <Box>
              <AlertTitle fontSize={{ base: "sm", md: "md" }}>Vista de Mantenimientos Futuros</AlertTitle>
              <AlertDescription fontSize={{ base: "sm", md: "md" }}>
                Mostrando solo los mantenimientos programados para el futuro. Estos pueden ser modificados antes de su ejecución.
              </AlertDescription>
            </Box>
          </Alert>
        )}

        {/* Lista de mantenimientos - Vista responsive */}
        <Card bg={cardBg} border="1px solid" borderColor={borderColor} overflow="hidden">
          <CardBody p={0}>
            {/* Vista de tabla para pantallas medianas y grandes */}
            <Box 
              display={{ base: 'none', md: 'block' }}
              overflowX="auto"
              css={{
                '&::-webkit-scrollbar': {
                  height: '8px',
                },
                '&::-webkit-scrollbar-track': {
                  background: 'transparent',
                },
                '&::-webkit-scrollbar-thumb': {
                  background: scrollbarThumb,
                  borderRadius: '4px',
                } as any,
              }}
            >
              <TableContainer minW="max-content">
                <Table variant="simple" size={{ base: 'sm', md: 'md' }}>
                  <Thead>
                    <Tr>
                      <Th whiteSpace="nowrap" px={{ base: 2, md: 4 }}>Vehículo</Th>
                      <Th whiteSpace="nowrap">Tipo</Th>
                      <Th whiteSpace="nowrap" px={{ base: 2, md: 4 }}>Estado</Th>
                      <Th whiteSpace="nowrap" display={{ base: 'none', lg: 'table-cell' }}>Prioridad</Th>
                      <Th whiteSpace="nowrap" display={{ base: 'none', xl: 'table-cell' }}>Programado</Th>
                      <Th whiteSpace="nowrap" display={{ base: 'none', xl: 'table-cell' }}>Completado</Th>
                      <Th whiteSpace="nowrap" display={{ base: 'none', lg: 'table-cell' }}>Km</Th>
                      <Th whiteSpace="nowrap" display={{ base: 'none', xl: 'table-cell' }}>Costo</Th>
                      <Th whiteSpace="nowrap" display={{ base: 'none', xl: 'table-cell' }}>Taller</Th>
                      <Th whiteSpace="nowrap" px={{ base: 2, md: 4 }}>Acciones</Th>
                    </Tr>
                  </Thead>
                  <Tbody>
                    {paginatedRecords.map((record) => (
                      <Tr 
                        key={record.id} 
                        _hover={{ bg: tableRowHover }}
                        bg={tableRowBg}
                      >
                        <Td px={{ base: 2, md: 4 }}>
                          <VStack spacing={0} align="start">
                            <Text fontWeight="bold" fontSize={{ base: 'xs', sm: 'sm' }} noOfLines={1}>
                              {record.vehiclePlate}
                            </Text>
                            <Text fontSize={{ base: '2xs', sm: 'xs' }} color={mutedTextColor} noOfLines={1}>
                              {record.vehicleBrand}
                            </Text>
                            {record.isFuture && (
                              <Badge colorScheme="purple" size="xs" mt={1}>
                                Futuro
                              </Badge>
                            )}
                          </VStack>
                        </Td>
                        <Td px={{ base: 2, md: 4 }}>
                          <Tooltip label={record.type} placement="top" isDisabled={record.type.length < 20}>
                            <Text fontSize={{ base: 'xs', sm: 'sm' }} noOfLines={1}>
                              {record.type}
                            </Text>
                          </Tooltip>
                        </Td>
                        <Td px={{ base: 2, md: 4 }}>
                          <Badge 
                            colorScheme={getStatusColor(record.status)} 
                            variant="subtle"
                            fontSize={{ base: '2xs', sm: 'xs' }}
                            px={2}
                            py={1}
                            borderRadius="md"
                            textTransform="none"
                          >
                            {getStatusText(record.status)}
                          </Badge>
                        </Td>
                        <Td display={{ base: 'none', lg: 'table-cell' }} px={{ base: 2, md: 4 }}>
                          <Badge 
                            colorScheme={getPriorityColor(record.priority)} 
                            variant="outline"
                            fontSize={{ base: '2xs', sm: 'xs' }}
                            px={2}
                            py={1}
                            borderRadius="md"
                          >
                            {getPriorityText(record.priority)}
                          </Badge>
                        </Td>
                        <Td display={{ base: 'none', xl: 'table-cell' }} px={{ base: 2, md: 4 }}>
                          <Text fontSize={{ base: 'xs', sm: 'sm' }} whiteSpace="nowrap">
                            {record.scheduledDate}
                          </Text>
                        </Td>
                        <Td display={{ base: 'none', xl: 'table-cell' }} px={{ base: 2, md: 4 }}>
                          <Text 
                            fontSize={{ base: 'xs', sm: 'sm' }} 
                            color={record.completedDate ? textColor : mutedTextColor}
                            whiteSpace="nowrap"
                          >
                            {record.completedDate || 'Pendiente'}
                          </Text>
                        </Td>
                        <Td display={{ base: 'none', lg: 'table-cell' }} px={{ base: 2, md: 4 }}>
                          <Text fontSize={{ base: 'xs', sm: 'sm' }} whiteSpace="nowrap">
                            {record.mileage.toLocaleString()} km
                          </Text>
                        </Td>
                        <Td display={{ base: 'none', xl: 'table-cell' }} px={{ base: 2, md: 4 }}>
                          <Text 
                            fontSize={{ base: 'xs', sm: 'sm' }} 
                            fontWeight="bold" 
                            color="green.500"
                            whiteSpace="nowrap"
                          >
                            ${record.cost.toFixed(2)}
                          </Text>
                        </Td>
                        <Td display={{ base: 'none', xl: 'table-cell' }} px={{ base: 2, md: 4 }}>
                          <Tooltip label={record.workshop} placement="top" isDisabled={record.workshop.length < 20}>
                            <Text fontSize={{ base: 'xs', sm: 'sm' }} noOfLines={1}>
                              {record.workshop}
                            </Text>
                          </Tooltip>
                        </Td>
                        <Td px={{ base: 2, md: 4 }}>
                          <HStack spacing={1}>
                            <Tooltip label="Ver detalles">
                              <IconButton
                                aria-label="Ver detalles"
                                icon={<Text fontSize="xs">👁️</Text>}
                                size="xs"
                                variant="ghost"
                                onClick={() => handleEdit(record)}
                              />
                            </Tooltip>
                            <Tooltip label="Editar">
                              <IconButton
                                aria-label="Modificar"
                                icon={<Text fontSize="xs">✏️</Text>}
                                size="xs"
                                variant="ghost"
                                onClick={() => handleEdit(record)}
                              />
                            </Tooltip>
                          </HStack>
                        </Td>
                      </Tr>
                    ))}
                  </Tbody>
                </Table>
              </TableContainer>
            </Box>

            {/* Vista de tarjetas para móviles */}
            <VStack 
              spacing={3} 
              p={4} 
              display={{ base: 'flex', md: 'none' }}
              align="stretch"
            >
              {paginatedRecords.map((record) => (
                <Card 
                  key={record.id} 
                  bg={tableRowBg} 
                  border="1px solid" 
                  borderColor={borderColor}
                  _hover={{ bg: tableRowHover }}
                  transition="all 0.2s"
                >
                  <CardBody p={4}>
                    <VStack spacing={3} align="stretch">
                      {/* Header de la tarjeta */}
                      <Flex justify="space-between" align="start">
                        <VStack spacing={1} align="start" flex={1}>
                          <HStack spacing={2}>
                            <Text fontWeight="bold" fontSize="sm">
                              {record.vehiclePlate}
                            </Text>
                            {record.isFuture && (
                              <Badge colorScheme="purple" size="sm">
                                Futuro
                              </Badge>
                            )}
                          </HStack>
                          <Text fontSize="xs" color={mutedTextColor}>
                            {record.vehicleBrand}
                          </Text>
                          <Text fontSize="sm" fontWeight="medium" color={textColor}>
                            {record.type}
                          </Text>
                        </VStack>
                        <HStack spacing={1}>
                          <IconButton
                            aria-label="Ver detalles"
                            icon={<Text fontSize="sm">👁️</Text>}
                            size="sm"
                            variant="ghost"
                            onClick={() => handleEdit(record)}
                          />
                          <IconButton
                            aria-label="Modificar"
                            icon={<Text fontSize="sm">✏️</Text>}
                            size="sm"
                            variant="ghost"
                            onClick={() => handleEdit(record)}
                          />
                        </HStack>
                      </Flex>

                      {/* Estado y prioridad */}
                      <HStack spacing={2} wrap="wrap">
                        <Badge 
                          colorScheme={getStatusColor(record.status)} 
                          variant="subtle"
                          fontSize="xs"
                          px={2}
                          py={1}
                          borderRadius="md"
                        >
                          {getStatusText(record.status)}
                        </Badge>
                        <Badge 
                          colorScheme={getPriorityColor(record.priority)} 
                          variant="outline"
                          fontSize="xs"
                          px={2}
                          py={1}
                          borderRadius="md"
                        >
                          {getPriorityText(record.priority)}
                        </Badge>
                      </HStack>

                      {/* Información adicional */}
                      <SimpleGrid columns={2} spacing={3} fontSize="xs">
                        <VStack spacing={1} align="start">
                          <Text color={mutedTextColor} fontWeight="medium">Programado:</Text>
                          <Text>{record.scheduledDate}</Text>
                        </VStack>
                        <VStack spacing={1} align="start">
                          <Text color={mutedTextColor} fontWeight="medium">Completado:</Text>
                          <Text color={record.completedDate ? textColor : mutedTextColor}>
                            {record.completedDate || 'Pendiente'}
                          </Text>
                        </VStack>
                        <VStack spacing={1} align="start">
                          <Text color={mutedTextColor} fontWeight="medium">Kilometraje:</Text>
                          <Text>{record.mileage.toLocaleString()} km</Text>
                        </VStack>
                        <VStack spacing={1} align="start">
                          <Text color={mutedTextColor} fontWeight="medium">Costo:</Text>
                          <Text fontWeight="bold" color="green.500">
                            ${record.cost.toFixed(2)}
                          </Text>
                        </VStack>
                      </SimpleGrid>

                      {/* Taller */}
                      <VStack spacing={1} align="start">
                        <Text color={mutedTextColor} fontWeight="medium" fontSize="xs">Taller:</Text>
                        <Text fontSize="sm">{record.workshop}</Text>
                      </VStack>

                      {/* Descripción si existe */}
                      {record.description && (
                        <VStack spacing={1} align="start">
                          <Text color={mutedTextColor} fontWeight="medium" fontSize="xs">Descripción:</Text>
                          <Text fontSize="sm" noOfLines={2}>{record.description}</Text>
                        </VStack>
                      )}
                    </VStack>
                  </CardBody>
                </Card>
              ))}
            </VStack>

            {/* Paginación */}
            {totalPages > 1 && (
              <Box 
                px={{ base: 3, md: 6 }}
                py={4}
                borderTop="1px solid"
                borderColor={borderColor}
              >
                <VStack spacing={3}>
                  <Text fontSize={{ base: "xs", sm: "sm" }} color={mutedTextColor} textAlign="center">
                    Mostrando {Math.min(paginatedRecords.length, itemsPerPage)} de {filteredRecords.length} registros
                  </Text>
                  
                  <HStack spacing={{ base: 1, sm: 2 }} wrap="wrap" justify="center">
                    <Button
                      size={{ base: "xs", sm: "sm" }}
                      onClick={() => setCurrentPage(1)}
                      isDisabled={currentPage === 1}
                      variant="outline"
                      fontSize={{ base: "xs", sm: "sm" }}
                    >
                      Primera
                    </Button>
                    <Button
                      size={{ base: "xs", sm: "sm" }}
                      onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))}
                      isDisabled={currentPage === 1}
                      fontSize={{ base: "xs", sm: "sm" }}
                    >
                      Anterior
                    </Button>
                    
                    <HStack spacing={1} display={{ base: 'none', sm: 'flex' }}>
                      {Array.from({ length: Math.min(5, totalPages) }, (_, i) => {
                        let pageNum: number;
                        if (totalPages <= 5) {
                          pageNum = i + 1;
                        } else if (currentPage <= 3) {
                          pageNum = i + 1;
                        } else if (currentPage >= totalPages - 2) {
                          pageNum = totalPages - 4 + i;
                        } else {
                          pageNum = currentPage - 2 + i;
                        }
                        
                        if (pageNum < 1 || pageNum > totalPages) return null;
                        
                        return (
                          <Button
                            key={pageNum}
                            size="sm"
                            variant={pageNum === currentPage ? 'solid' : 'outline'}
                            colorScheme={pageNum === currentPage ? 'blue' : 'gray'}
                            onClick={() => setCurrentPage(pageNum)}
                            minW="40px"
                          >
                            {pageNum}
                          </Button>
                        );
                      })}
                      {totalPages > 5 && currentPage < totalPages - 2 && (
                        <Text px={2} color={mutedTextColor}>...</Text>
                      )}
                    </HStack>
                    
                    <Text fontSize={{ base: "xs", sm: "sm" }} color={mutedTextColor} display={{ base: 'block', sm: 'none' }}>
                      {currentPage}/{totalPages}
                    </Text>
                    
                    <Button
                      size={{ base: "xs", sm: "sm" }}
                      onClick={() => setCurrentPage(prev => Math.min(prev + 1, totalPages))}
                      isDisabled={currentPage === totalPages}
                      fontSize={{ base: "xs", sm: "sm" }}
                    >
                      Siguiente
                    </Button>
                    <Button
                      size={{ base: "xs", sm: "sm" }}
                      onClick={() => setCurrentPage(totalPages)}
                      isDisabled={currentPage === totalPages}
                      variant="outline"
                      fontSize={{ base: "xs", sm: "sm" }}
                    >
                      Última
                    </Button>
                  </HStack>
                  
                  <Text fontSize="sm" color={mutedTextColor} display={{ base: 'none', sm: 'block' }}>
                    Página {currentPage} de {totalPages}
                  </Text>
                </VStack>
              </Box>
            )}
          </CardBody>
        </Card>
      </VStack>

      {/* Modal para editar/agregar mantenimiento */}
      <Modal 
        isOpen={isOpen} 
        onClose={onClose} 
        size={{ base: "full", sm: "lg", md: "xl" }}
        scrollBehavior="inside"
      >
        <ModalOverlay />
        <ModalContent 
          mx={{ base: 0, sm: 4 }}
          my={{ base: 0, sm: 8 }}
          maxH={{ base: "100vh", sm: "90vh" }}
        >
          <ModalHeader 
            fontSize={{ base: "lg", md: "xl" }}
            px={{ base: 4, md: 6 }}
            py={{ base: 3, md: 4 }}
          >
            {isEditing ? 'Modificar Registro de Mantenimiento' : 
             isCurrentMaintenance ? 'Registrar Mantenimiento Actual' : 'Programar Mantenimiento Futuro'}
          </ModalHeader>
          <ModalCloseButton size={{ base: "sm", md: "md" }} />
          <ModalBody px={{ base: 4, md: 6 }} py={{ base: 3, md: 4 }}>
            <VStack spacing={{ base: 3, md: 4 }}>
              {!isEditing && (
                <Alert status={isCurrentMaintenance ? "success" : "info"} borderRadius="md">
                  <AlertIcon />
                  <Box>
                    <AlertTitle fontSize="sm">
                      {isCurrentMaintenance ? 'Mantenimiento Actual' : 'Mantenimiento Futuro'}
                    </AlertTitle>
                    <AlertDescription fontSize="sm">
                      {isCurrentMaintenance ? 
                        'Este mantenimiento se registrará como realizado o en curso actualmente.' :
                        'Este mantenimiento será marcado como "futuro" para diferenciarlo de los registros históricos.'}
                    </AlertDescription>
                  </Box>
                </Alert>
              )}

              <SimpleGrid columns={{ base: 1, md: 2 }} spacing={{ base: 3, md: 4 }}>
                <FormControl>
                  <FormLabel fontSize={{ base: "sm", md: "md" }}>Vehículo</FormLabel>
                  <Select placeholder="Seleccionar vehículo" size={{ base: "sm", md: "md" }}>
                    <option value="ABC-123">ABC-123 - Volvo FH16</option>
                    <option value="DEF-456">DEF-456 - Mercedes Actros</option>
                    <option value="GHI-789">GHI-789 - Ford Transit</option>
                  </Select>
                </FormControl>

                <FormControl>
                  <FormLabel fontSize={{ base: "sm", md: "md" }}>Tipo de Mantenimiento</FormLabel>
                  <Select placeholder="Seleccionar tipo" size={{ base: "sm", md: "md" }}>
                    <option value="Cambio de aceite">Cambio de aceite</option>
                    <option value="Revisión general">Revisión general</option>
                    <option value="Cambio de llantas">Cambio de llantas</option>
                    <option value="Mantenimiento preventivo">Mantenimiento preventivo</option>
                    <option value="Reparación de motor">Reparación de motor</option>
                    <option value="Sistema de frenos">Sistema de frenos</option>
                  </Select>
                </FormControl>

                <FormControl>
                  <FormLabel fontSize={{ base: "sm", md: "md" }}>
                    {isCurrentMaintenance ? 'Fecha de Realización' : 'Fecha Programada'}
                  </FormLabel>
                  <Input 
                    type="date" 
                    defaultValue={isCurrentMaintenance ? new Date().toISOString().split('T')[0] : ''} 
                    size={{ base: "sm", md: "md" }}
                  />
                </FormControl>

                {isCurrentMaintenance && (
                  <FormControl>
                    <FormLabel fontSize={{ base: "sm", md: "md" }}>Estado del Mantenimiento</FormLabel>
                    <Select defaultValue="completed" size={{ base: "sm", md: "md" }}>
                      <option value="completed">Completado</option>
                      <option value="in-progress">En Progreso</option>
                    </Select>
                  </FormControl>
                )}

                <FormControl>
                  <FormLabel fontSize={{ base: "sm", md: "md" }}>Prioridad</FormLabel>
                  <Select size={{ base: "sm", md: "md" }}>
                    <option value="low">Baja</option>
                    <option value="normal">Normal</option>
                    <option value="high">Alta</option>
                    <option value="urgent">Urgente</option>
                  </Select>
                </FormControl>

                <FormControl>
                  <FormLabel fontSize={{ base: "sm", md: "md" }}>Kilometraje Estimado</FormLabel>
                  <Input 
                    type="number" 
                    placeholder="Ej: 50000" 
                    size={{ base: "sm", md: "md" }}
                  />
                </FormControl>

                <FormControl>
                  <FormLabel fontSize={{ base: "sm", md: "md" }}>Costo Estimado</FormLabel>
                  <Input 
                    type="number" 
                    placeholder="Ej: 250.00" 
                    size={{ base: "sm", md: "md" }}
                  />
                </FormControl>

                <FormControl>
                  <FormLabel fontSize={{ base: "sm", md: "md" }}>Taller</FormLabel>
                  <Select size={{ base: "sm", md: "md" }}>
                    <option value="Taller Central">Taller Central</option>
                    <option value="Taller Norte">Taller Norte</option>
                    <option value="Taller Sur">Taller Sur</option>
                    <option value="Taller Externo">Taller Externo</option>
                  </Select>
                </FormControl>
              </SimpleGrid>

              <FormControl>
                <FormLabel fontSize={{ base: "sm", md: "md" }}>Descripción</FormLabel>
                <Textarea 
                  placeholder="Descripción detallada del mantenimiento..." 
                  size={{ base: "sm", md: "md" }}
                  rows={4}
                />
              </FormControl>
            </VStack>
          </ModalBody>

          <ModalFooter 
            px={{ base: 4, md: 6 }} 
            py={{ base: 3, md: 4 }}
            flexDirection={{ base: "column", sm: "row" }}
            gap={{ base: 2, sm: 0 }}
          >
            <Button 
              variant="ghost" 
              mr={{ base: 0, sm: 3 }} 
              onClick={onClose}
              size={{ base: "sm", md: "md" }}
              w={{ base: "full", sm: "auto" }}
              order={{ base: 2, sm: 1 }}
            >
              Cancelar
            </Button>
            <Button 
              colorScheme="blue" 
              onClick={handleSave}
              size={{ base: "sm", md: "md" }}
              w={{ base: "full", sm: "auto" }}
              order={{ base: 1, sm: 2 }}
            >
              {isEditing ? 'Guardar Cambios' : 
               isCurrentMaintenance ? 'Registrar Mantenimiento' : 'Programar Mantenimiento'}
            </Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </Container>
  );
};

export default Maintenance;
